import React from "react";
import { Icon as LegacyIcon, Dropdown } from "@ant-design/compatible";
import { Button, Menu } from "antd";

const CDropdownTable = ({ items }) => {
  return (
    <Dropdown
      overlay={
        <Menu>
          {items.map((item) => {
            return (
              <Menu.Item onClick={item.action} key={item.name}>
                <LegacyIcon type={item.icon} />
                {item.name}
              </Menu.Item>
            );
          })}
        </Menu>
      }
      placement="bottomRight"
    >
      <Button type="primary" size="small">
        <LegacyIcon type="control" />
        <LegacyIcon type="down" />
      </Button>
    </Dropdown>
  );
};

export default CDropdownTable;
